<template>
<!-- :style="{'paddingBottom':$route.meta.paddingFlag?'1.33333rem':'0'}" -->
  <div id="sort">
    <div class="sort_search">
      <van-search v-model="value" placeholder="搜索商品信息" @search="onSearch" />
    </div>
    <!-- 主区 -->
    <div class="sort_main">
      <!-- 滚动导航 -->
      <van-tabs v-model="active" scrollspy sticky>
        <van-tab v-for="i in goods" :title="i.cate_name" :key="i.id">
          <van-divider :style="{ color: '#333', borderColor: '#999', padding: '0 1.5rem' }">
              {{i.cate_name}}
          </van-divider>
          <template>
            <div class="sort_i">
              <div class="sort_ul" v-for="d in i.children" :key="d.id">
                <GoodsPsd :item="d"></GoodsPsd>
              </div>
            </div>
          </template>
        </van-tab>
    </van-tabs>
    </div>
  </div>
</template>
<script>
import GoodsPsd from '../components/sort/GoodsPsd.vue'
export default {
  components:{
    GoodsPsd,
  },
  data(){
    return{
      indexList:[],
      goods:[],
      // 滚动导航
      active: 0,
      // 搜索框
      value: '',
    }
  },
  created(){
    this.axios.get("http://47.115.51.185/api/category").then(res=>{
      console.log(res);
      let data=res.data.data;
      this.goods = data;
      // console.log(this.goods);
      this.indexList = data.map(d=>{
        return d.cate_name;
      })
      // console.log(this.indexList);
    })
  },
  methods:{
    onSearch(){
      // console.log(this.value);
      let key = this.value;
      this.$router.push({name:'GoodsList',query:{key:key}});
    }
  }
}
</script>
<style lang="less">
#sort{
  width: 100vw;
  box-sizing: border-box;
  padding-top: 1.4rem;
  background-color: #fff !important;
  .sort_search{
    width: 100vw;
    height: 1.4rem;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    border-bottom: 1px solid #f5f5f5;
    // 搜索外框
    .van-search{
      border-bottom: 1px solid #f5f5f5;
      .van-search__content{
        background-color: #f5f5f5;
        border-radius: 0.5rem;
        .van-cell{
          // 搜索图标
          .van-field__left-icon{
            .van-icon{
              color: #555;
              font-size: 0.5rem;
            }
          }
        }
      }
    }
  }
  // 搜索框结束
  // 滚动导航
  .sort_main{
    width: 100vw;
    box-sizing: border-box;
    // 导航开始
    .van-tabs{
      // 导航标签开始
      div{
        .van-sticky{
          position: fixed;
          top: 1.4rem;
          z-index: 0;
          background-color: #f5f5f5;
          // 高度，滚动
          .van-tabs__wrap,.van-tabs__nav{
            height: calc(100vh - 1.4rem - 1.33333rem);
            .van-tabs__nav{
              display: block;
              width: 2.4rem;
              overflow-y: auto;
              overflow-x: hidden;
              padding: 0 0;
              background-color: #f5f5f5;
              // 标签文字盒子
              .van-tab{
                // 文字
                background-color: #f5f5f5;
                height: 1rem;
                &.van-tab--active{
                  color: #fc4141;
                  font-weight: 700;
                  background-color: #fff;
                  border-left: 0.04rem solid #fc4141;
                }
                .van-tab__text{
                  
                }
              }
              // 线
              .van-tabs__line{
                display: none;
              }
            }
          }
        }
      }
      // 导航内容
      .van-tabs__content{
        // 导航文字
        width: 100vw;
        box-sizing: border-box;
        padding-left: 2.4rem;
        position: relative;
        z-index: 1;
        .van-tab__pane{
          background: #fff;
          // padding: 1rem 0;
          padding-bottom: 0.3rem;
          // 线
          .van-divider{
            font-size: 0.28rem;
            font-weight: 700;
            color: #333;
            padding: 0.5rem 1.5rem !important;
            margin: 0 0;
          }
          // 
          .sort_i{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-around;
            .sort_ul{
              width: 30%;
                  .googsPsd{
                    .goodsPsd_b{
                      color: #333;
                      font-size: 0.24rem;
                    }
                  }
            }
          }
        }
      }
    }
  }
}
</style>